<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/index' }"><b>首页</b></el-breadcrumb-item>
        <el-breadcrumb-item>外出管理</el-breadcrumb-item>
        <el-breadcrumb-item>外出申请</el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" >
    <!-- 查询新增栏 -->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true">
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-col>

    <el-table :data="workouts" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
                style="width: 100%;" v-if="tableshow" height="380">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" label="序号" width="80"></el-table-column> 
      <el-table-column prop="userId" label="工号" sortable></el-table-column>
      <el-table-column prop="userName" label="姓名" sortable></el-table-column>
      <el-table-column prop="roleName" label="职位" sortable></el-table-column>        
      <el-table-column prop="departmentName" label="部门" sortable></el-table-column>
      <el-table-column prop="phoneNum" label="联系方式" sortable></el-table-column>
      <el-table-column prop="email" label="邮箱" sortable></el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <p style="margin:5px;"><el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button></p>
          <p style="margin:5px;"><el-button type="danger" @click="delUser(scope.$index,scope.row)" size="small">删除</el-button></p>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页显示 -->
    <el-col :span="24" class="toolbar" style="margin-top: 10px;" v-if="tableshow">
      <!-- <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0">批量删除</el-button> -->
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pagesize" :total="total"
                      style="float:right;">
      </el-pagination>
    </el-col>
  
  
  
  </el-row>



</template>
<script>
    export default{
        data(){
          return {
            tableshow: true
          }
        }
    }
</script>
<style>

</style>


