import Vue from "vue";
import Router from "vue-router";

// import Login from "../components/login.vue";
import Index from "../components/index.vue";
import MainLogo from "../components/mainlogo.vue";

import LeaveApply from "../components/leave/leaveapply.vue";
import LeaveList from "../components/leave/leavelist.vue";
import MealsApply from "../components/meals/mealsapply.vue";
import MealsList from "../components/meals/mealslist.vue";
import PersonSet from "../components/setting/personset.vue";
import WorkFlowSet from "../components/setting/workflowset.vue";
// import UserAdd from "../components/user/useradd.vue";
import UserList from "../components/user/userlist.vue";
import PlanWork from "../components/work/planwork.vue";
import RealWork from "../components/work/realwork.vue";
import WorkoutApply from "../components/workout/workoutapply.vue";
import WorkoutList from "../components/workout/workoutlist.vue";

import {delCookie,getCookie} from "@/cookies.js";


const Login = resolve => require(["@/components/login"], resolve);

Vue.use(Router);

let routers = new Router({
  mode: "history",
  routes: [
    {
      path: "/login",
      name: "登录",
      component: Login
    },
    {
      path: "/index",
      name: "index",
      component: Index,
      children: [
        {
          path: "/index",
          name: 'mainlogo',
          component: MainLogo 
        },
        {
          path: "/leave/leaveapply",
          name: "leaveapply",
          component: LeaveApply
        },
        {
          path: "/leave/leavelist",
          name: "leavelist",
          component: LeaveList
        },
        {
          path: "/meals/mealsapply",
          name: "mealsapply",
          component: MealsApply
        },
        {
          path: "/meals/mealslist",
          name: "mealslist",
          component: MealsList
        },
        {
          path: "/setting/personset",
          name: "personset",
          component: PersonSet
        },
        {
          path: "/setting/workflowset",
          name: "workflowset",
          component: WorkFlowSet
        },
        // {
        //   path: "/api/user/useradd",
        //   name: "useradd",
        //   component: UserAdd
        // },
        {
          path: "/user/userlist",
          name: "userlist",
          component: UserList
        },
        {
          path: "/work/planwork",
          name: "planwork",
          component: PlanWork
        },
        {
          path: "/work/realwork",
          name: "realwork",
          component: RealWork
        },
        {
          path: "/workout/workoutapply",
          name: "workoutapply",
          component: WorkoutApply
        },
        {
          path: "/workout/workoutlist",
          name: "workoutlist",
          component: WorkoutList
        }
      ]
    }
  ]
});

routers.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith("/login")) {
    window.localStorage.removeItem("access-user");
    next();
  }
  // else if(JSON.parse(window.localStorage.getItem("access-user"))) {
  //   next({ path: "/index" });
  // }
  else{
    let user = JSON.parse(window.localStorage.getItem("access-user"));
    if (!user) {
      next({ path: "/login" });
    } else {
      next();
    }
  }

});

export default routers;



