<template>
    <div id="index">
        <header>
            <h1>办公管理自动化系统</h1>
            <div class="userInfo">
                <div>
                    <p v-text="user.name"></p>
                    <p v-text="user.role"></p>
                </div>
                <el-dropdown>
                    <img src="../assets/img/userlogo.png" alt="" width="70px" height="70px">
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item><router-link to="/api/setting/personset"><span>个人信息</span></router-link></el-dropdown-item>
                        <el-dropdown-item @click.native="logout">登出</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </header>
        <div style="background-color:#fff;width:100%;overflow:auto;position:absolute;top:100px;bottom:0px;" class="clearfix">
            <nav>
                <el-menu class="el-menu-vertical-demo" :collapse="isCollapse" :unique-opened="isUniqueOpened" >
                    <div class="el-menu-item navbar">
                        <i class="fa fa-bars" aria-hidden="true" @click="tabNav"></i>
                    </div>

                    <!-- test 二级菜单 -->
                    <!-- <template v-for="(menu,index) in usermenus">
                        <el-submenu :index="index+''">
                            <template slot="title">
                                <i class="fa" v-bind:class="menu.faico"></i>
                                <span slot="title"><router-link :to="menu.url">{{menu.menutitle}}</router-link></span>
                            </template>
                            <el-submenu v-for="(submenu,index) in menu.submenus" :index="index+''+index+''" :key="index">
                                <span slot="title">{{submenu.subtitle}}</span>
                                <el-menu-item v-for="(subsubmenu,index) in submenu.subsubmenus" :index="index+''+index+''+index+''" :key="index">
                                   <router-link :to="{path:'/api/user/userlist',query: {departmentId: subsubmenu.departmentId}}"> {{subsubmenu.subsubtitle}}</router-link>
                                </el-menu-item>
                            </el-submenu>
                        </el-submenu>
                    </template> -->
                    <!-- test -->

                    <!-- 一级菜单 -->
                    <template v-for="(menu,index) in menus">
                        <el-submenu :index="index+''">
                            <template slot="title">
                                <i class="fa" v-bind:class="menu.faico"></i>
                                <span slot="title">{{menu.menutitle}}</span>
                            </template>
                            <el-menu-item v-for="(submenu,index) in menu.submenus" :index="index+''" :key="index"><router-link :to="submenu.url">{{submenu.menutitle}}</router-link></el-menu-item>
                        </el-submenu>
                    </template>
                                        
                </el-menu>
            </nav>
            <div :class="{ isactive: isActive ,main: isMain}">
                <router-view></router-view>
                <!-- <footer><p>Copyright &copy; 2017 OA Power By Team9.</p></footer> -->
            </div>
        </div>
    </div>
</template>
<script>
import routers from '../router/routers.js'
import {getCookie,delCookie} from "../cookies.js"
export default {
    name: 'index',
    data() {
        return {
            workid: '',
            isCollapse: false,
            isUniqueOpened: true,
            isActive: false,
            isMain: true,
            user: {
                name: '',
                role: ''
            },
            menus: ''
            
        }
    },
    methods: {
        tabNav: function() {
            this.isCollapse = !this.isCollapse;
            this.isActive = !this.isActive;
            this.isMain = !this.isMain;
        },
        logout: function() {
            var _this = this;
            this.$confirm('确认退出吗?', '提示', {}).then(() => {
                localStorage.removeItem('access-user');
                // sessionStorage.removeItem('access-user');
                // delCookie(this.workid);
                // if(getCookie("rem")){
                //     delCookie("rem");
                // }
                _this.$router.push('/api/login');
            }).catch(() => {
            });
        }
    },
    beforeCreate: function() {
        // var user = JSON.parse(window.sessionStorage.getItem("access-user"));
        var user = JSON.parse(window.localStorage.getItem("access-user"));
        this.workid = user.userId;
        // this.workid = getCookie("id");
        // this.workid = this.$route.params.userId;
        // console.log(this.workid);
        //start-indexInitReq
        this.$http({
            url: '/api/index',
            method: 'POST',
            emulateJSON: true,
            body: {
                userId: this.workid
            },
            headers: {
                "content-type": "application/x-www-form-urlencoded"
            },
            timeout: 2000,
            before: function(request) {
                // console.log("request in before");
            }
        }).then(
            function(res) {
                this.user.name = res.body.data.name;
                // console.log(this.user.name);
                this.user.role = res.body.data.role;
                this.menus = res.body.data.menus;
                console.log(res);
                // console.log("success");
                this.$router.push({name: 'mainlogo'});
            },
            function(res) {
                console.log("failed");
            }
        );
        //end-indexInitReq
    },
    beforeDestroy: function () {
        window.localStorage.removeItem("access-user");
    }
}
</script>
<style>
#index {
    width: 100%;
    height: 100%;
    background-color: #169cd6;
    color: #fffefe;
    font-family: "微软雅黑";
    /* font-weight: 500; */
}
/*#index header {
    width: 100%;
    height: 100px;
    background-color: #169cd6;
    background-image: url(../assets/img/oalogo1.png);
    background-repeat: no-repeat;
    background-position: 24px 24px;
    background-size: 82px;
}*/
#index header h1 {
    float: left;
    font-size: 30px;
    margin-top: 45px;
    margin-left: 110px;
}
#index header .userInfo {
    float: right;
    width: 200px;
    height: 100px;
    /* background-color: lightcoral; */
}
#index header .userInfo div:first-of-type {
    float: left;
    width: 104px;
    /*height: 100px;*/
    vertical-align: middle;
    font-size: 18px;
    padding: 26px 0px;
    line-height: 24px;
}
.userInfo div p {
    text-align: center;
    font-weight: 300;
}
#index header .userInfo img  {
    float: right;
    margin-right: 24px;
    margin-top: 16px;
}
#index header .userInfo  .el-dropdown{
    float: right;
    width:70px;
}
#index nav .el-menu-vertical-demo:not(.el-menu--collapse) {
    /* float: left; */
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    width: 15%;
    /* height: 800px; */
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #354152;
    /* margin-bottom: -10000px;
    padding-bottom: 10000px; */
}
#index nav .el-menu-vertical-demo {
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    /* width: 5%; */
    /* height: 800px; */
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #354152;
    /* margin-bottom: -10000px;
    padding-bottom: 10000px; */
}
#index nav ul {
    /* width: 56px; */
}
#index .main {
    box-sizing: border-box;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    width: 85%;
    background-color: #fff;
    overflow: auto;
    color: #000;
    padding: 10px;
    /* margin-bottom: -10000px;
    padding-bottom: 10000px; */
}
#index .isactive {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    width: 95%;
    background-color: #fff;
    overflow: hidden;
    /* margin-bottom: -10000px;
    padding-bottom: 10000px; */
}
#index .el-menu {
    border-radius: 0px;
}
#index nav .navbar {
    text-align: center;
    font-size: 22px;
    line-height: 56px;
}
#index nav .el-menu-item {
    background-color: #3d4859;
    color: #fff;
}
#index nav .el-menu-item-group__title {
    padding: 0;
}
#index nav .el-submenu__title {
    background-color: #354152;
    color: #fff;
}
.el-dropdown-menu{
    text-align: center;
    color:#169CD6;
}
.el-dropdown-menu__item a{
    color:#169CD6;
}
a {
    color: #fff;
}
footer {
    position: absolute;
    bottom: 10px;
    width:100%;
    text-align:center;
    color: #999;
}
</style>


