<template>
    <el-row class="warp">
      <el-col :span="24" class="warp-breadcrum">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/index' }"><b>首页</b></el-breadcrumb-item>
          <el-breadcrumb-item>员工信息</el-breadcrumb-item>
          <el-breadcrumb-item>员工列表</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    <el-col :span="24" class="warp-main" >
      <!-- 查询新增栏 -->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.username" placeholder="请输入员工名字" style="width:120px;"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="filters.userId" placeholder="请输入员工工号" style="width:120px;"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select clearable @clear="disabledselect=false" v-model="filters.companyId" placeholder="请选择分公司"style="width:140px;" @visible-change="getDepartments">
              <el-option
                v-for="item in companys"
                :key="item.departmentId"
                :label="item.departmentName"
                :value="item.departmentId">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="filters.departmentId" placeholder="请选择部门"style="width:140px;" :disabled="disabledselect==false" clearable>
              <el-option
                v-for="item in departments"
                :key="item.departmentId"
                :label="item.departmentName"
                :value="item.departmentId">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item>
            <el-select v-model="filters.rolename" placeholder="请选择角色"style="width:140px;" clearable>
              <el-option
                v-for="item in rolenames"
                :key="item.rolename"
                :label="item.rolename"
                :value="item.rolename">
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item>
            <el-button type="primary" v-on:click="getUsers">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-col>

    <!-- 列表 -->
    <el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
                style="width: 100%;" v-if="tableshow" height="380">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" label="序号" width="80"></el-table-column> 
      <el-table-column prop="userId" label="员工工号" sortable></el-table-column>
      <el-table-column prop="userName" label="员工名字" sortable></el-table-column>
      <el-table-column prop="roleName" label="员工职位" sortable></el-table-column>
      <el-table-column prop="companyName" label="所属公司" sortable></el-table-column>        
      <el-table-column prop="departmentName" label="所属部门" sortable :formatter="depformmatter"></el-table-column>
      <el-table-column prop="phoneNum" label="联系方式" sortable></el-table-column>
      <el-table-column prop="email" label="邮箱" sortable></el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <p style="margin:5px;"><el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button></p>
          <p style="margin:5px;"><el-button type="danger" @click="delUser(scope.$index,scope.row)" size="small">删除</el-button></p>
        </template>
      </el-table-column>
    </el-table>
      <!-- 列表 -->

      <!--工具条-->
      <el-col :span="24" class="toolbar" style="margin-top: 10px;" v-if="tableshow">
        <!-- <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0">批量删除</el-button> -->
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pagesize" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="员工工号" prop="name">
            <el-input v-model="editForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="员工姓名" prop="author">
            <el-input v-model="editForm.author" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="员工职位">
            <!-- <el-date-picker type="date" placeholder="选择日期" v-model="editForm.publishAt"></el-date-picker> -->
          </el-form-item>
          <el-form-item label="所属公司" prop="description">
            <!-- <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input> -->
          </el-form-item>
          <el-form-item label="所属部门" prop="description">
            <!-- <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input> -->
          </el-form-item>
          <el-form-item label="联系方式" prop="description">
            <!-- <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input> -->
          </el-form-item>
          <el-form-item label="电子邮箱" prop="description">
            <!-- <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input> -->
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
        </div>
      </el-dialog>
      <!-- 工具条 -->

      <!--新增界面-->
      <el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="员工名字" prop="userName">
            <el-input v-model="addForm.userName" auto-complete="off" placeholder="请输入员工名字"></el-input>
          </el-form-item>
          <el-form-item label="角色" prop="rolename">
            <el-select v-model="addForm.rolename" placeholder="请选择角色" clearable>
              <el-option
                v-for="item in rolenames"
                :key="item.rolename"
                :label="item.rolename"
                :value="item.rolename">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所属公司" prop="companyId" :required="requiredcom==true">
            <el-select clearable @clear="newdisabledselect=false"  v-model="addForm.companyId" placeholder="请选择分公司" @visible-change="getDepartments">
              <el-option
                v-for="item in companys"
                :key="item.departmentId"
                :label="item.departmentName"
                :value="item.departmentId">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所属部门" prop="departmentId" :required="requireddep==true">
            <el-select v-model="addForm.departmentId" placeholder="请选择部门" :disabled="newdisabledselect==false" clearable>
              <el-option
                v-for="item in departments"
                :key="item.departmentId"
                :label="item.departmentName"
                :value="item.departmentId">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="联系方式" prop="phoneNum">
            <el-input v-model="addForm.phoneNum" auto-complete="off"  placeholder="请输入联系方式"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addForm.email" auto-complete="off"  placeholder="请输入邮箱"></el-input>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>


  </el-row>
</template>
<script>
    export default{
        data(){
          // 校验用户角色为董事长和部门经理
          var validaterolename = (rule, value, callback) => {
            // console.log(value);
            if(value=='董事长'){
              callback(new Error('无法选择董事长'));
            }
            else if (value == '总经理'){
              console.log('总经理')
              this.requiredcom = true;
              this.requireddep = false;
              this.newdisabledselect=false;
              this.addForm.departmentId = '';
              this.addForm.rolename = value;
              callback();
            }else if(value == ''){
              callback(new Error("请选择角色"));
            }
            else{
              this.requiredcom = true;
              this.requireddep = true;
              this.newdisabledselect=true;
              this.addForm.rolename = value;
              callback();
            }
          };

          //校验公司;
          var validatecom = (rule,value,callback) => {
            // alert("公司:"+value)
            if(this.requiredcom&&value==''){
              callback(new Error('请选择分公司'));
            }else{
              this.addForm.companyId = value;
              callback();
            }
          }

          var validatedep = (rule,value,callback) => {
            if(this.requireddep&&value==''){
              callback(new Error('请选择部门'));
            }else{
              this.addForm.departmentId = value;
              callback();
            }
          }

          return {
            filters: {
              username: '',
              userId: '',
              companyId: '',
              departmentId: '',
              rolename: ''
            },
            disabledselect : false,
            newdisabledselect: false,
            users: [],
            total: 0,
            pagesize: 0,
            page: 1,
            listLoading: false,
            sels: [], //列表选中列
            tableshow: false, //table初始化不显示

            //编辑相关数据
            editFormVisible: false,//编辑界面是否显示
            editLoading: false,
            editFormRules: {
              name: [
                {required: true, message: '请输入书名', trigger: 'blur'}
              ],
              
              author: [
                {required: true, message: '请输入作者', trigger: 'blur'}
              ],
              description: [
                {required: true, message: '请输入简介', trigger: 'blur'}
              ]
            },
            editForm: {
              id: 0,
              name: '',
              author: '',
              publishAt: '',
              description: ''
            },
            //新增相关数据
            addFormVisible: false,//新增界面是否显示
            addLoading: false,
            addFormRules: {
              userName: [
                {required: true, message: '请输员工姓名', trigger: 'blur'}
              ],
              companyId: [
                {required: this.requiredcom, trigger: 'blur', validator: validatecom}
              ],
              departmentId: [
                {required: this.requireddep, trigger: 'blur', validator: validatedep}
              ],
              rolename: [
                {required: true, trigger: 'blur', validator: validaterolename}
              ]
              // ,
              // phoneNum: [
              //   {required: true, message: '请输入联系方式', trigger: 'blur'}
              // ],
              // email: [
              //   {required: true, message: '请输入邮箱', trigger: 'blur'}
              // ]
            },
            addForm: {
              userName: '',
              companyId: '',
              departmentId: '',
              rolename: ''
              // ,
              // phoneNum: '',
              // email: ''
            },
            companys:[],
            departments: [],
            rolenames: [],
            companyId: '',
            requiredcom: false,
            requireddep: false
          }
        },
        methods: {
          depformmatter: function(row, column){
            const arr = row[column.property]
            console.log("arr:"+arr)
            if(arr == 201701){
              return "广州";
            }else if(arr == 201702){
              return "珠海";
            }else if(arr == 201703){
              return "东莞";
            }
          },
          selsChange: function (sels) {
            this.sels = sels;
          },
          // batchDeleteBook: function () {
            
          // },
          handleCurrentChange:function(val) {
            this.page = val;
            console.log("val page:"+this.page)
            this.getUsers();
          },
          showEditDialog: function (index, row) {
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
          },
          delUser: function (index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
              this.listLoading = true;
              let para = {id: row.id};
              reqDeleteBook(para).then((res) => {
                this.listLoading = false;
                this.$message({
                  message: '删除成功',
                  type: 'success'
                });
                this.getBooks();
              });
            }).catch(() => {
            });
            // this.$http({

            // }).then(
            //   function(res) {

            //   },
            //   function(res) {

            //   }
            // )
          },
          showAddDialog: function () {
            this.getCompanys();
            this.addFormVisible = true;
            this.newdisabledselect = false;
            this.addForm = {
              // userId: '',
              userName: '',
              companyId: '',
              departmentId: '',
              rolename: '',
              phoneNum: '',
              email: ''
            };
            this.requiredcom = false;
            this.requireddep = false;
          },
          getUsers: function(){
            // console.log(this.$route.query.departmentId);
            // this.filters.departmentId = this.$route.query.departmentId
            // console.log("departmentId: "+this.filters.departmentId);
            // if(this.filters.departmentId){
            //   this.listLoading = true;
            //     this.$http({
            //         url: '/api/user/userlist'+'?departmentId='+this.filters.departmentId,
            //         method: 'GET',
            //         emulateJSON: true,
            //         headers: {
            //             "content-type": "application/x-www-form-urlencoded"
            //         },
            //         timeout: 2000,
            //         before: function(request) {
            //             console.log("request in before");
            //         }
            //     }).then(
            //         function(res) {
            //             console.log("下面是userlist的res")
            //             console.log(res);
            //             this.total = res.body.total;
            //             this.users = res.body.users;
            //             this.listLoading = false;
            //             this.tableshow = true;
            //         },
            //         function(res) {
            //             console.log("failed");
            //             this.listLoading = false;
            //             this.$alert("加载数据失败", "Error", {
            //               confirmButtonText: '确定',
            //               callback: action => { }
            //             });
            //         }
            //     );
            // }else{ 
              this.tableshow=true;
              this.listLoading = true;
              this.$http({
                    url: '/api/user/userlist',
                    method: 'POST',
                    body: {
                      page: this.page,
                      username: this.filters.username,
                      userId: this.filters.userId,
                      companyId: this.filters.companyId,
                      departmentId: this.filters.departmentId,
                      rolename: this.filters.rolename
                    },
                    emulateJSON: true,
                    headers: {
                        "content-type": "application/x-www-form-urlencoded"
                    },
                    timeout: 2000,
                    before: function(request) {
                        console.log("request in before");
                    }
                }).then(
                    function(res) {
                        console.log(res);
                        this.total = res.body.total;
                        this.users = res.body.users;
                        this.pagesize = res.body.pagesize;
                        this.listLoading = false;
                        this.tableshow = true;
                    },
                    function(res) {
                        console.log("failed");
                        this.listLoading = false;
                        this.$alert("加载数据失败", "Error", {
                          confirmButtonText: '确定',
                          callback: action => { }
                        });
                    }
                );
            // }
            
          },
          getCompanys: function(){
            this.$http({
              url: '/api/department/listTopDept',
              method: 'GET',
              timeout: 2000,
            }).then(
              function(res){
                console.log("获取公司")
                console.log(res)
                if(res.body.status==1){
                  this.companys = res.body.data;
                }
              },
              function(res){
                console.log("failed")
              }
            )
          },
          getDepartments: function(){
            // if(!this.filters.companyId) return false;
            if(this.filters.companyId){
              console.log("this.filters.companyId:"+this.filters.companyId);
              this.companyId = this.filters.companyId;
            }
            if(this.addForm.companyId){
              console.log("this.addform.companyId:"+this.addForm.companyId);
              this.companyId = this.addForm.companyId;
            }
            this.$http({              
              url: '/api/department/listChildByDeptId?deptId='+this.companyId,
              method: 'GET',
              timeout: 2000,
            }).then(
              function(res){
                console.log("获取部门")
                console.log(res)
                if(res.body.status==1){
                  this.departments = res.body.data;
                  this.disabledselect = true,
                  this.newdisabledselect = true
                }
              },
              function(res){
                console.log("failed")
              }
            )
          },
          getRolename: function(){
            this.$http({
              url: '/api/rolename',
              method: 'GET',
              timeout: 2000,
            }).then(
              function(res){
                console.log("success")
                console.log("获取角色")
                console.log(res)
                if(res.body.status==1){
                  this.rolenames = res.body.data;
                }
              },
              function(res){
                console.log("failed")
              }
            )
          },
          addSubmit: function () {
            this.$refs.addForm.validate((valid) => {
              if (valid) {
                // this.addLoading = true;

            console.log('新增提交数据');
            console.log(this.addForm);
              }
            });
          },
        },
        beforeCreate: function(){
          
        },
        cereated:function(){
          // console.log(this.$route.query.departmentId);
          // this.getUsers();
        },
        mounted:function () {
          this.getCompanys();
          this.getRolename();
        },
        watch: {
          "$route": "getUsers"
        }
    }
</script>
<style>
</style>


